<template>
	<div>
		<div class="main list-container contents">
			<h1 class="page-header">채팅방 리스트</h1>
			<div v-if="isLoading">Loading...</div>
			<LoadingSpinner v-if="isLoading"></LoadingSpinner>
			<ul v-else>
				<WaitingroomListItem
					v-for="waitingroomItem in waitingroomItems"
					:key="waitingroomItem._id"
					:waitingroomItem="waitingroomItem"
					@refresh="fetchData"
				></WaitingroomListItem>
			</ul>
		</div>
		<router-link to="/add" class="create-button">
			<i class="ion-md-add"></i>
		</router-link>
	</div>
</template>

<script>
import WaitingroomListItem from '@/components/waitingrooms/WaitingroomListItem.vue';
import LoadingSpinner from '@/components/common/LoadingSpinner.vue';
import { fetchWaitingRooms } from '@/api/waitingrooms';

export default {
	components: {
		WaitingroomListItem,
		LoadingSpinner,
	},
	data() {
		return {
			waitingroomItems: [],
			isLoading: false,
		};
	},
	methods: {
		async fetchData() {
			this.isLoading = true;
			const { data } = await fetchWaitingRooms();
			this.isLoading = false;
			this.waitingroomItems = data.waitingrooms;
		},
	},
	created() {
		this.fetchData();
	},
};
</script>

