// waiting 조작과 관련된 CRUD API 함수 파일
import { waitingrooms } from './index';

// waitingroom 리스트 데이터 조회 API
function fetchWaitingRooms() {
	return waitingrooms.get('/');
}

// waitingroom 데이터 생성하는 API
function createWaitingroom(WaitingroomData) {
	return waitingrooms.post('/', WaitingroomData);
}

export { fetchWaitingRooms, createWaitingroom };
